<script>
export default {
  name: 'GxTrialBalanceLedgerAccounts',
  data() {
    return {}
  },
  props: {
    ledgerAccounts: {
      type: Array,
      default: null,
    },
    level: {
      type: Number,
      default: 1,
    },
  },
  methods: {
    getNumberFormat(text) {
      if (text === '') return '-'
      if (text === '0') return '-'
      if (text === '0.00') return '-'
      if (text.includes('.')) {
        return text
          .replace(/^\d+\.$/, '$&00')
          .replace(/\d(?=(\d{3})+\.)/g, '$&,')
      } else {
        //return (
        text = text
          .replace(/^\d+\.$/, '$&00')
          .replace(/\.\d$/, '0$&0')
          .replace(/\d+\.$/, '$&00')
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
        if (text == '0') return ''
        if (text == '0' || text == '') return text
        return text + '.00'
        //)
      }
    },
    getNature(nature) {
      return nature === 'debit' ? 'Deudora' : 'Acreedora'
    },
    getNumberOfAccountsByParts(ledgerAccount) {
      return ledgerAccount.number.split('-')
    },
    getSubAccountsCount(ledgerAccount) {
      if (ledgerAccount.subAccounts === undefined) return 0
      if (ledgerAccount.subAccounts === null) return 0
      return ledgerAccount.subAccounts.length
    },
    onSelected(ledgerAccount) {
      if (this.getSubAccountsCount(ledgerAccount) === 0)
        this.$emit('onSelected', ledgerAccount)
    },
  },
}
</script>

<template>
  <ul>
    <li
      v-for="ledgerAccount in ledgerAccounts"
      :key="ledgerAccount.ledgerAccountId"
      @click.prevent.stop="onSelected(ledgerAccount)"
    >
      <div
        class="group flex align-middle rounded pt-1 pb-1 borderLedgerAccount"
        :class="{
          'cursor-pointer': getSubAccountsCount(ledgerAccount) === 0,
          'hover:bg-ledgerAccountlist-hover':
            getSubAccountsCount(ledgerAccount) === 0,
        }"
      >
        <div class="ml-2.5 w-5 h-5 text-center">
          <span
            v-if="level === 1"
            class="font-bold text-2xs w-5 block mt-1"
            :class="[
              'text-ledgerAccountTypes-' + ledgerAccount.ledgerAccountType,
            ]"
            >M</span
          >
          <span v-else class="w-5 block"></span>
        </div>
        <div class="inline-flex text-white">
          <span
            class="pt-1 pb-2px mr-px font-bold text-2xs"
            :index="idx"
            :nivel="getNumberOfAccountsByParts(ledgerAccount).length"
            v-for="(item, idx) in getNumberOfAccountsByParts(ledgerAccount)"
            :class="[
              'bg-ledgerAccountTypes-' + ledgerAccount.ledgerAccountType,
              {
                'rounded-tl rounded-bl pl-2 pr-1': idx === 0,
                'pl-1 pr-2 rounded-r':
                  idx ===
                    getNumberOfAccountsByParts(ledgerAccount).length - 1 &&
                  !getSubAccountsCount(ledgerAccount) > 0,
                'pl-2 pr-2':
                  (idx !== 0 &&
                    idx !==
                      getNumberOfAccountsByParts(ledgerAccount).length - 1) ||
                  (idx !== 0 &&
                    idx ==
                      getNumberOfAccountsByParts(ledgerAccount).length - 1 &&
                    getSubAccountsCount(ledgerAccount) > 0),
              },
            ]"
            :key="idx"
          >
            {{ item }}
          </span>
        </div>
        <div
          class="inline-flex pl-5 pr-2 pt-2px sm:hover:bg-secondary-20 w-full"
        >
          <div class="flex w-auto inline-block resize-none max-w-4xl">
            <span
              class="whitespace-no-wrap w-full truncate overflow-hidden text-xs"
              :class="{
                'font-bold': getSubAccountsCount(ledgerAccount) > 0,
              }"
            >
              {{ ledgerAccount.name }}</span
            >
          </div>
        </div>
        <div class="flex">
          <div
            class="flex w-23 items-center justify-center"
            :class="{
              'font-bold': getSubAccountsCount(ledgerAccount) > 0,
            }"
          >
            {{ getNature(ledgerAccount.nature) }}</div
          >
          <div
            class="flex w-23 items-center justify-end pr-2"
            :class="{
              'font-bold': getSubAccountsCount(ledgerAccount) > 0,
            }"
            >{{ getNumberFormat(ledgerAccount.beginningBalance.toString()) }}
          </div>
          <div
            class="flex w-23 items-center justify-end pr-2"
            :class="{
              'font-bold': getSubAccountsCount(ledgerAccount) > 0,
            }"
            >{{ getNumberFormat(ledgerAccount.credit.toString()) }}</div
          >
          <div
            class="flex w-23 items-center justify-end pr-2"
            :class="{
              'font-bold': getSubAccountsCount(ledgerAccount) > 0,
            }"
            >{{ getNumberFormat(ledgerAccount.debit.toString()) }}</div
          >
          <div
            class="flex w-23 items-center justify-end pr-2"
            :class="{
              'font-bold': getSubAccountsCount(ledgerAccount) > 0,
            }"
            >{{ getNumberFormat(ledgerAccount.finalBalance.toString()) }}</div
          >
        </div>
      </div>

      <GxTrialBalanceLedgerAccounts
        v-if="getSubAccountsCount(ledgerAccount) > 0"
        :ledgerAccounts="
          ledgerAccount.subAccounts === null ? [] : ledgerAccount.subAccounts
        "
        @onSelected="onSelected"
        :level="level + 1"
      />
    </li>
  </ul>
</template>

<style>
.borderLedgerAccount {
  border-style: solid;
  border-color: #e0e0e0;
  border-right-color: #fafafa;
  border-left-color: #fafafa;
  border-bottom-width: 1px;
  border-right-width: 0.5px;
  border-left-width: 0.5px;
}
</style>
