.gx-button {
  --initial-width: initial;

  min-width: 2rem;
  min-height: 3.125rem;
  font-family: 'Nunito';
  font-size: 1.125rem;
  line-height: 1.75rem;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  // Make loading end transition .1 seconds slower since it has just an icon and it looks like it's snapping faster on exit.
  transition: all 0.6s ease-in-out;
}
.gx-button--plain {
  min-width: initial;
  min-height: 24px;
}
.gx-button--plain .slotContainer {
  top: -2px;
}
.gx-button i {
  font-size: inherit;
}

@screen de {
  .variant-button {
    min-height: 1.75rem;
    padding-right: 0.5rem;
    padding-left: 0.5rem;
    font-family: 'Nunito';
    font-size: 0.75rem;
  }
  .gx-button {
    min-height: 2.75rem;
    font-size: 0.75rem;
  }
  .slotContainer {
    top: 0;
    letter-spacing: 0;
  }
  .gx-button--plain .slotContainer {
    top: 0;
  }
  .gx-height-variant {
    min-height: 1.5rem;
  }
  .gx-height-variant .slotContainer {
    top: -1px;
  }
  .gx-height-variant-secondary {
    min-width: 0;
    min-height: 1.125rem;
    font-size: 0.625rem;
    line-height: 15px;
    text-transform: none;
  }
}

// Ripple

/* Ripple effect */
.ripple {
  --ripple-x: 0;
  --ripple-y: 0;
  --ripple-background: white;

  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
}

.ripple::after {
  position: absolute;
  top: calc(var(--ripple-y) - 32.5px);
  left: calc(var(--ripple-x) - 31px);
  display: none;
  width: 61px;
  height: 65px;
  pointer-events: none;
  content: '';
  background-color: var(--ripple-background);
  border-radius: 50%;
  opacity: 0;
  transform-origin: center center;
  animation: ripple 1s;
}

.ripple:focus:not(:active)::after {
  display: block;
}

@keyframes ripple {
  from {
    opacity: 0.5;
    transform: scale(0);
  }
  to {
    opacity: 0;
    transform: scale(10);
  }
}
// Loader
@keyframes spin {
  from {
    background: transparent;
    border: 2px solid var(--secondary-300);
    border-top-color: var(--primary-500);
    transform: rotate(0deg);
  }
  to {
    background: transparent;
    border: 2px solid var(--secondary-300);
    border-top-color: var(--primary-500);
    transform: rotate(360deg);
  }
}

.loading {
  width: 50px;
  min-width: 50px;
  height: 50px;
  transition: all 3s ease-in-out;
  animation-name: spin;
  animation-duration: 0.6s;
  animation-timing-function: linear;
  animation-delay: 1.3s;
  animation-iteration-count: infinite;
}

@screen de {
  .loading {
    width: 44px;
    min-width: 44px;
    height: 44px;
  }
}
